.navbar{
    background : rgba(0,0,0,0.3);
    width : max-content;
    display : block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    bottom:2rem;
    gap: 0.8rem;
    border-radius:3rem;
    backdrop-filter: blur(15px);

    .navbar__item{
        background : transparent;
        padding: 0.9rem;
        border-radius: 50%;
        display: flex;
        color : var(--color-light);
        font-size: 1.6rem;
        border:none !important;
        &:hover{
            background : rgba(0,0,0,0.3);
        }
    
    }

    .navbar__item.active{
        background: var(--color-bg  );
        color : var(--color-white);
    }
}